@import url("https://fonts.googleapis.com/css2?family=Oswald:wght@400;700&family=Roboto&display=swap");
.mediabase .table tr td, .nav-primary .menu-primary li .dropdown-menu li, select option, select, input.form-control, label.form-check-label, p, ol li,
ul li {
  font-family: "Roboto", Helvetia, Arial, sans-serif;
  font-weight: 400;
}

.downloads table tr td:first-of-type, input[type=submit], label.form-label, .btn, h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Oswald", Arial, Helvetica, sans-serif;
  font-weight: 700;
}

.mediabase .table tr th, .nav-primary .menu-primary li {
  font-family: "Oswald", Arial, Helvetica, sans-serif;
  font-weight: 400;
}

html {
  font-size: 62.5%;
}

* {
  border: none;
  box-sizing: border-box;
  outline: none;
}

img {
  display: block;
  height: auto;
  max-width: 100%;
}

.site-inner {
  padding-top: 24px;
}
.site-inner .content {
  margin-bottom: 48px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 28px;
  text-transform: uppercase;
}
h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
  text-decoration: none;
  transition: all 0.2s ease;
}
h1 {
  font-size: 4.2rem !important;
}

h2 {
  font-size: 3.6rem !important;
}

h3 {
  font-size: 3.2rem !important;
}

h4 {
  font-size: 2.8rem !important;
}

h5 {
  font-size: 2.4rem !important;
}

h6 {
  font-size: 1.8rem !important;
}

p, ol li,
ul li {
  margin-top: 0;
  margin-bottom: 28px;
  font-size: 1.6rem;
  line-height: 170%;
}
p a, ol li a,
ul li a {
  transition: all 0.2s ease;
}
ol li,
ul li {
  margin-bottom: 8px;
}
ol li a,
ul li a {
  text-decoration: none;
}
.btn {
  font-size: 1.6rem;
  margin-bottom: 0;
  text-transform: uppercase;
}

label.form-label {
  font-size: 1.8rem;
  text-transform: uppercase;
}
label.form-check-label {
  font-size: 1.6rem;
}

input.form-control {
  font-size: 1.6rem;
}
input[type=submit] {
  font-size: 1.6rem;
  text-transform: uppercase;
}

select {
  font-size: 1.6rem !important;
}
select option {
  font-size: 1.6rem;
}

.form-login {
  margin-bottom: 28px;
}
.form-login div {
  margin-bottom: 24px;
}

.form-register div {
  margin-bottom: 24px;
}

.frame-container {
  height: 0;
  position: relative;
  padding-bottom: 56.25%;
}
.frame-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}
.frame-container.frame-container-muzooka {
  padding-bottom: 940%;
}
@media (min-width: 400px) {
  .frame-container.frame-container-muzooka {
    padding-bottom: 772%;
  }
}
@media (min-width: 576px) {
  .frame-container.frame-container-muzooka {
    padding-bottom: 734%;
  }
}
@media (min-width: 768px) {
  .frame-container.frame-container-muzooka {
    padding-bottom: 662%;
  }
}
@media (min-width: 992px) {
  .frame-container.frame-container-muzooka {
    padding-bottom: 300%;
  }
}
@media (min-width: 1200px) {
  .frame-container.frame-container-muzooka {
    padding-bottom: 175%;
  }
}
@media (min-width: 1400px) {
  .frame-container.frame-container-muzooka {
    padding-bottom: 158%;
  }
}

.site-header {
  padding-top: 18px;
  padding-bottom: 18px;
  background-color: #eee;
}
@media (min-width: 992px) {
  .site-header .mobile-menu-btn {
    display: none;
  }
}
.site-header .title-area .site-title {
  margin-top: 0;
  margin-bottom: 0;
  max-width: 270px;
}

.nav-primary .menu-primary li {
  font-size: 1.6rem;
  margin-bottom: 0;
  text-transform: uppercase;
}
.nav-primary .menu-primary li .dropdown-menu li {
  text-transform: capitalize;
}

.nav-header {
  display: none;
}
@media (min-width: 992px) {
  .nav-header {
    display: block;
  }
}

#offcanvas-navigation .offcanvas-title {
  max-width: 270px;
}

.menu-socials li {
  margin-bottom: 0;
}

.site-footer {
  padding-top: 12px;
  padding-bottom: 12px;
  background-color: #000;
}
.site-footer p, .site-footer ol li, ol .site-footer li,
.site-footer ul li,
ul .site-footer li {
  color: #fff;
  font-size: 1.4rem;
  margin-bottom: 0;
}

.downloads table h4 {
  margin-bottom: 18px;
}
.downloads table p, .downloads table ol li, ol .downloads table li,
.downloads table ul li,
ul .downloads table li {
  line-height: 100%;
  margin-bottom: 0;
}
.downloads table p.song-label, .downloads table ol li.song-label, ol .downloads table li.song-label,
.downloads table ul li.song-label,
ul .downloads table li.song-label {
  font-size: 1.6rem;
  margin-bottom: 12px;
}
.downloads table p.song-label .record-label, .downloads table ol li.song-label .record-label, ol .downloads table li.song-label .record-label,
.downloads table ul li.song-label .record-label,
ul .downloads table li.song-label .record-label {
  font-size: 1.4rem;
  font-style: italic;
}
.downloads table tr td {
  display: block;
}
@media (min-width: 768px) {
  .downloads table tr td {
    display: table-cell;
  }
}
.downloads table tr td:first-of-type {
  font-size: 3.6rem;
  border-bottom: none;
}
@media (min-width: 768px) {
  .downloads table tr td:first-of-type {
    border-bottom: 1px solid #dee2e6;
    width: 8.333%;
  }
}
.downloads table tr td:nth-of-type(2) {
  border-bottom: none;
}
@media (min-width: 768px) {
  .downloads table tr td:nth-of-type(2) {
    border-bottom: 1px solid #dee2e6;
  }
}
.downloads #offcanvas-music-download h4 {
  font-size: 2.4rem !important;
  margin-top: 18px;
}
.downloads #offcanvas-music-download table tr td {
  border-bottom: 1px solid #dee2e6;
}

.mediabase .table tr th {
  font-size: 1.4rem;
  text-transform: uppercase;
}
.mediabase .table tr td {
  font-size: 1.6rem;
}